body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {

}

.lineaTiempo{
  border-bottom: 1px solid black;
  display: inline-block;
  width: 65%;
  margin: 0px auto;
  position: relative;
}
.contenedorTiempo{
  text-align: center;
}
.contenedorTiempo #inicial, .contenedorTiempo #final{
  width: 5em;
  margin: 0 20px;
}

.contenedorTiempo .formAdd input{
  display:block;
  margin: 5px auto;
}
.espacioTrabajo{
  margin-top: 150px;
}
.elemento{
  position: absolute;
  text-align: center;
  bottom: -7px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.elemento i{
  text-align: center;
  font-size: 10px;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.2s ease;
}
.elemento i:hover{
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

